"use client"

import { useState, useEffect } from 'react'
import { Card } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { marketingBot } from '@/lib/marketingBot'
import { LoadingSpinner } from '@/components/LoadingSpinner'

interface DashboardStats {
  products: number;
  campaigns: number;
  transactions: number;
  affiliates: number;
  revenue: number;
}

const initialStats: DashboardStats = {
  products: 0,
  campaigns: 0,
  transactions: 0,
  affiliates: 0,
  revenue: 0
}

export default function Dashboard() {
  const [isLoading, setIsLoading] = useState<boolean>(false)
  const [isConnected, setIsConnected] = useState<boolean>(false)
  const [stats, setStats] = useState<DashboardStats>(initialStats)
  const [error, setError] = useState<string | null>(null)
  const [isLoadingStats, setIsLoadingStats] = useState<boolean>(false)

  const connectBot = async () => {
    setIsLoading(true)
    setError(null)
    try {
      const success = await marketingBot.login()
      setIsConnected(success)
      if (!success) {
        setError('Failed to connect to the bot')
      }
    } catch (error) {
      console.error('Failed to connect bot:', error)
      setError('Connection error occurred')
      setIsConnected(false)
    } finally {
      setIsLoading(false)
    }
  }

  const fetchStats = async () => {
    if (!isConnected) return

    setIsLoadingStats(true)
    setError(null)

    const fetchWithTimeout = async (promise: Promise<any>, timeout: number = 5000) => {
      const timeoutPromise = new Promise((_, reject) => {
        setTimeout(() => reject(new Error('Request timed out')), timeout);
      });
      return Promise.race([promise, timeoutPromise]);
    };

    try {
      const [products, campaigns, transactions, affiliates] = await Promise.allSettled([
        fetchWithTimeout(marketingBot.getProducts()),
        fetchWithTimeout(marketingBot.getCampaigns()),
        fetchWithTimeout(marketingBot.getTransactions()),
        fetchWithTimeout(marketingBot.getAffiliateStats())
      ]);

      const results = {
        products: products.status === 'fulfilled' ? products.value : [],
        campaigns: campaigns.status === 'fulfilled' ? campaigns.value : [],
        transactions: transactions.status === 'fulfilled' ? transactions.value : [],
        affiliates: affiliates.status === 'fulfilled' ? affiliates.value : { totalAffiliates: 0 }
      };

      const failedRequests = [products, campaigns, transactions, affiliates]
        .filter(result => result.status === 'rejected')
        .map(result => (result as PromiseRejectedResult).reason);

      if (failedRequests.length > 0) {
        const timeoutErrors = failedRequests.filter(error => error.message === 'Request timed out').length;
        const errorMessage = timeoutErrors > 0
          ? `${timeoutErrors} request${timeoutErrors > 1 ? 's' : ''} timed out. Some data may be incomplete.`
          : `${failedRequests.length} data source${failedRequests.length > 1 ? 's' : ''} failed to load. Some information may be incomplete.`;
        setError(errorMessage);
      }

      setStats({
        products: results.products.length || 0,
        campaigns: results.campaigns.length || 0,
        transactions: results.transactions.length || 0,
        affiliates: results.affiliates?.totalAffiliates || 0,
        revenue: results.transactions.reduce((sum: number, t: any) => sum + (t.amount || 0), 0)
      });
    } catch (error) {
      console.error('Failed to fetch stats:', error);
      setError('Failed to fetch latest statistics. Please try again later.');
    } finally {
      setIsLoadingStats(false);
    }
  };

  useEffect(() => {
    let mounted = true;
    let reconnectAttempts = 0;
    let reconnectTimeout: NodeJS.Timeout;

    const attemptReconnect = async () => {
      if (!mounted || isConnected || reconnectAttempts >= 3) return;
      
      reconnectAttempts++;
      await connectBot();
      
      if (!isConnected && mounted) {
        reconnectTimeout = setTimeout(attemptReconnect, 5000);
      }
    };

    attemptReconnect();

    return () => {
      mounted = false;
      if (reconnectTimeout) {
        clearTimeout(reconnectTimeout);
      }
    };
  }, [isConnected])

  useEffect(() => {
    let mounted = true;
    let interval: NodeJS.Timeout;

    const updateStats = async () => {
      if (mounted && isConnected) {
        await fetchStats();
      }
    };

    if (isConnected) {
      updateStats();
      // Refresh every 30 seconds if connected
      interval = setInterval(updateStats, 30000);
    } else {
      // Reset stats when disconnected
      setStats(initialStats);
      setError(null);
    }

    return () => {
      mounted = false;
      if (interval) {
        clearInterval(interval);
      }
    };
  }, [isConnected]);

  return (
    <div className="p-8">
      {/* Header */}
      <div className="space-y-4 mb-8">
        <div className="flex justify-between items-center">
          <h1 className="text-3xl font-bold">Marketing Bot Dashboard</h1>
          <Button
            onClick={connectBot}
            disabled={isLoading}
            className={`${
              isConnected ? 'bg-green-600' : 'bg-black'
            } hover:bg-opacity-90 transition-colors relative`}
          >
            {isLoading && (
              <span className="absolute left-2">
                <LoadingSpinner />
              </span>
            )}
            <span className="ml-6">
              {isLoading ? 'Connecting...' : isConnected ? 'Connected' : 'Connect Bot'}
            </span>
          </Button>
        </div>
      {error && (
        <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-lg relative" role="alert">
          <div className="flex items-center">
            <svg className="w-5 h-5 mr-2" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd"/>
            </svg>
            <span className="block sm:inline">{error}</span>
          </div>
        </div>
      )}
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-6 mb-8">
        <Card className="p-6 relative">
          {isLoadingStats && <LoadingSpinner />}
          <h3 className="text-sm font-medium text-gray-500">Total Products</h3>
          <p className="text-2xl font-bold mt-2">{stats.products}</p>
        </Card>
        <Card className="p-6 relative">
          {isLoadingStats && <LoadingSpinner />}
          <h3 className="text-sm font-medium text-gray-500">Active Campaigns</h3>
          <p className="text-2xl font-bold mt-2">{stats.campaigns}</p>
        </Card>
        <Card className="p-6 relative">
          {isLoadingStats && <LoadingSpinner />}
          <h3 className="text-sm font-medium text-gray-500">Transactions</h3>
          <p className="text-2xl font-bold mt-2">{stats.transactions}</p>
        </Card>
        <Card className="p-6 relative">
          {isLoadingStats && <LoadingSpinner />}
          <h3 className="text-sm font-medium text-gray-500">Active Affiliates</h3>
          <p className="text-2xl font-bold mt-2">{stats.affiliates}</p>
        </Card>
        <Card className="p-6 relative">
          {isLoadingStats && <LoadingSpinner />}
          <h3 className="text-sm font-medium text-gray-500">Total Revenue</h3>
          <p className="text-2xl font-bold mt-2">${stats.revenue.toFixed(2)}</p>
        </Card>
      </div>

      {/* Features Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {/* Product Management */}
        <Card className="p-6">
          <h3 className="text-xl font-semibold mb-4">Product Management</h3>
          <p className="text-gray-600 mb-4">
            Create and manage your products automatically
          </p>
          <div className="space-y-2">
            <Button className="w-full bg-black hover:bg-gray-800">
              Add New Product
            </Button>
            <Button className="w-full bg-gray-100 text-black hover:bg-gray-200">
              View Products
            </Button>
          </div>
        </Card>

        {/* Campaign Management */}
        <Card className="p-6">
          <h3 className="text-xl font-semibold mb-4">Advertising</h3>
          <p className="text-gray-600 mb-4">
            Manage your advertising campaigns
          </p>
          <div className="space-y-2">
            <Button className="w-full bg-black hover:bg-gray-800">
              Create Campaign
            </Button>
            <Button className="w-full bg-gray-100 text-black hover:bg-gray-200">
              View Campaigns
            </Button>
          </div>
        </Card>

        {/* Payment Processing */}
        <Card className="p-6">
          <h3 className="text-xl font-semibold mb-4">Payments</h3>
          <p className="text-gray-600 mb-4">
            Monitor transactions and revenue
          </p>
          <div className="space-y-2">
            <Button className="w-full bg-black hover:bg-gray-800">
              Process Payment
            </Button>
            <Button className="w-full bg-gray-100 text-black hover:bg-gray-200">
              View Transactions
            </Button>
          </div>
        </Card>

        {/* Affiliate Management */}
        <Card className="p-6">
          <h3 className="text-xl font-semibold mb-4">Affiliate Program</h3>
          <p className="text-gray-600 mb-4">
            Manage your affiliate marketing program
          </p>
          <div className="space-y-2">
            <Button className="w-full bg-black hover:bg-gray-800">
              Add Affiliate
            </Button>
            <Button className="w-full bg-gray-100 text-black hover:bg-gray-200">
              View Affiliates
            </Button>
          </div>
        </Card>

        {/* AI Research */}
        <Card className="p-6">
          <h3 className="text-xl font-semibold mb-4">AI Product Research</h3>
          <p className="text-gray-600 mb-4">
            Get AI-powered product recommendations
          </p>
          <div className="space-y-2">
            <Button className="w-full bg-black hover:bg-gray-800">
              Research Products
            </Button>
            <Button className="w-full bg-gray-100 text-black hover:bg-gray-200">
              View Insights
            </Button>
          </div>
        </Card>

        {/* Analytics */}
        <Card className="p-6">
          <h3 className="text-xl font-semibold mb-4">Analytics</h3>
          <p className="text-gray-600 mb-4">
            Track performance metrics and ROI
          </p>
          <div className="space-y-2">
            <Button className="w-full bg-black hover:bg-gray-800">
              Generate Report
            </Button>
            <Button className="w-full bg-gray-100 text-black hover:bg-gray-200">
              View Analytics
            </Button>
          </div>
        </Card>
      </div>
    </div>
  )
}
